 body{overflow-x:hidden;margin:0;padding:0}#projects{width:100vw;height:100vh;padding:20px;display:flex;flex-direction:column;align-items:center;text-align:center;color:#ffffff;box-sizing:border-box;background:url('../../public/assets/images/redsun.gif') center/cover no-repeat;user-select:none}.projects-heading{font-size:3rem;margin-bottom:20px;color:#1c8dff;user-select:none}.projects-page{display:flex;justify-content:center;gap:50px;margin-bottom:30px;width:100%;position:relative;transition:opacity .5s ease-in-out}.fade-out{opacity:0}.fade-in{opacity:1}.project-card{background-color:#0e101c;border-radius:10px;padding:20px;cursor:pointer;text-align:center;color:#f1f1f1;width:360px;height:500px;box-sizing:border-box;user-select:none;transition:transform .6s ease-in-out,box-shadow .6s ease-in-out;animation:float 3s ease-in-out infinite;box-shadow:0 0 30px 10px rgba(28,141,255,.3);display:flex;flex-direction:column;justify-content:space-between}.project-card:hover{box-shadow:0 0 50px 20px rgba(28,141,255,.6)}.project-card-img{width:100%;height:230px;object-fit:cover;border-radius:10px;margin-bottom:15px}.project-card-title{font-size:1.6rem;margin-bottom:5px;color:#1c8dff;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.project-icons{display:flex;justify-content:center;gap:10px;margin-bottom:15px}.tech-icon-proj{font-size:1.8rem;color:#1c8dff}.project-links{display:flex;justify-content:center;gap:15px;margin-top:auto}.project-link-button{font-size:.9rem;padding:10px 25px;border:2px solid #1c8dff;background:transparent;color:#1c8dff;cursor:pointer;border-radius:5px;text-decoration:none;transition:background-color .3s ease,color .3s ease;user-select:none;white-space:nowrap}.project-link-button:hover{background-color:#1c8dff;color:#333}.page-indicator{display:flex;gap:10px;justify-content:center;margin-top:20px}.page-dot{width:15px;height:15px;background-color:#555;border-radius:50%;cursor:pointer;transition:background-color .3s ease}.page-dot.active{background-color:#1c8dff}.page-dot:hover{background-color:#777}.nav-arrow{visibility:hidden}@keyframes float{0%,100%{transform:translateY(0)}50%{transform:translateY(-10px)}}@media (max-width:768px){#projects{width:100vw;padding:20px;box-sizing:border-box;color:#ffffff;overflow-x:hidden;text-align:center}.projects-heading{font-size:2rem;margin-bottom:20px;color:#2ec1ff}.projects-page{display:flex;flex-direction:column;align-items:center;width:100%}.project-card{width:100%;max-width:400px;padding:20px;background-color:#141313;border-radius:10px;box-shadow:0 4px 10px rgba(0,0,0,.3);display:flex;flex-direction:column;align-items:center;margin-bottom:20px}.project-card-img{width:100%;height:auto;max-height:250px;object-fit:cover;border-radius:8px;margin-bottom:15px}.project-card-title{font-size:1.6rem;color:#2ec1ff;margin-bottom:10px}.tech-icons-proj{display:flex;flex-wrap:wrap;gap:10px;justify-content:center;margin-bottom:15px}.project-link-button{width:100%;padding:12px;font-size:1rem;color:#ffffff;background-color:#05aef7af;border:none;border-radius:8px;cursor:pointer;transition:background-color .3s ease}.project-link-button:hover{background-color:#1b89b856}.page-navigation{display:flex;justify-content:center;align-items:center;gap:10px;margin-top:10px}.nav-arrow{font-size:1.8rem;color:whitesmoke;cursor:pointer;transition:color .3s ease;visibility:visible;margin-top:-60px}.nav-arrow.disabled{color:#555;cursor:not-allowed}.page-indicator{display:flex;gap:8px;margin-top:-60px}.page-dot{width:12px;height:12px;background-color:#c9c9c9;border-radius:50%;cursor:pointer}.page-dot.active{background-color:#2ec1ff}.tech-icon-proj{font-size:3rem;color:whitesmoke}}