 #landing{position:relative;width:100%;height:100vh;background:black;display:flex;flex-direction:column;align-items:center;justify-content:center;overflow:hidden}.landing-content{text-align:center;color:#ffffff;z-index:2}.landing-name{font-size:5rem;font-weight:bold;margin:-30px}.landing-position{font-size:2rem;margin-top:20px;color:#ffffff}.landing-navigation{position:absolute;bottom:20px;text-align:center;cursor:pointer}.scroll-icon{font-size:2.5rem;color:#ffffff;transition:color .3s ease,transform .3s ease}.scroll-icon:hover{color:#2ec1ff;transform:translateY(10px)}.landing-links{position:absolute;bottom:410px;display:flex;gap:40px;color:#ffffff;font-weight:bold;text-transform:uppercase;font-size:1.2rem}.landing-links a{color:#ffffff;text-decoration:none;transition:color .3s ease}.landing-links a:hover{color:#2ec1ff}.logo-social-links{position:absolute;bottom:20px;left:20px;display:flex;align-items:center;gap:20px}.landing-logo-bottom{width:40px;height:40px}.social-icon{font-size:2rem;color:#ffffff;transition:color .3s ease}.social-icon:hover{color:#2ec1ff}.copyright-text{position:absolute;bottom:20px;right:20px;color:#ffffff;font-size:1rem;opacity:.8}@media (max-width:768px){.landing-name{font-size:3rem;margin:0}.landing-position{font-size:1.5rem;margin-top:15px;margin-bottom:20px}.landing-links{position:absolute;bottom:110px;right:60px;gap:20px;display:flex;flex-direction:row;align-items:flex-end}.logo-social-links{left:10px;bottom:100px;gap:15px}.copyright-text{position:absolute;bottom:10px;left:50%;transform:translateX(-50%);width:100%}}